  transition: transform .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.mobile-menu.open {
  transform: translateX(0);
}

.mob-menu-btn-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mob-menu-btn-close .mob-menu-close {
  transition: transform .3s ease-in-out;
}

.mob-menu-btn-close:hover {
